export const title: string = `家出の話 ―裏―`;
export const novel: string = `※捏造注意
　父は、娘を可愛がっていた。
　子供ながらも、彼女が生まれたときから気づいていたことだ。_できたばかりの妹は、虚ろな瞳で父を見つめた。父は娘を優しく撫で、見たこともない顔で微笑んだ。
　だが近頃、それがあまりにも顕著になっていた。理由は簡単だ。クレハが、外で遊び出すようになったから。行ってきますと優しく微笑み、元気よく走り去る姿を微笑みかえしながらも心配そうに見送り、父は仕事へ戻る。_ミトスの民のみんなと、よく話し合いをしているのだ。
　俺が出歩くようになったときは、気をつけての一言もなかった。それが当たり前だと思っていたから、その時は特に何も思わなかったのだが。
　途端に、自分が彼を、ミトスの長を父と呼んでいることが可笑しくなった。おかしく思えて、笑いが止まらなくなるかと思った。どこからかやって来た大人が、どこか具合でも悪いのかと尋ねたのに顔をあげる。端からみれば、単に俯いていただけらしい。何も告げずに、そこから逃げた。
　人間が神殿と呼ぶ建物を飛び出し、木々を抜けた森の中にほんの少し開けたところがあり、花や背の低い草が無作為に生えている。そこでいつもクレハとその友達はなにやら行っていた。
　「あ、兄さん。」
　クレハは蕾を模すように閉じた両手を広げ、そこから青い蝶が飛び立つ様を見せにきた。
　「ああっせっかく捕まえたのにっ。」
　少し離れて遊んでいた一人が叫ぶ。
　驚いて目を開けば、悪戯っぽく笑う。いつもなら、今までなら愛しさが溢れたのであろうが、無邪気に笑う妹が、今日は何故だか忌々しく思えた。
　「どこいくの。」
　クレハに尋ねられたが、足を止める気はない。
　「どっか景色のいいところ。」
　その後は彼女の友達が呼び、再び輪の中に入っていったし、振り向くことも面倒だったから知らない。特に行くあてもなく、さまよった。
　風で木々が音をたてるのを後ろに聞きながら 、目の前に両手を広げる町を見た。装飾がとれかけた門をくぐると、心のない人形が出迎えた。なんだか今は気分がよくない。この人形を亜空間にでも放ってやったら、気分も晴れるだろうか。
　町は騒がしかった。なにか珍しいものが入ったのだろうか、喧嘩でも始まったのだろうか。人々はこんなにも広い通りを無駄にするように一所に集まって、手をあげ声をあげ、ときどきものもあがった。
　醜い。人間は、あまりにも。
　いつか父から聞いたことがあった。近頃人間は、ミトスの民を邪魔者のように見るのだと。これが災いに繋がらなければ良いがとも、付け加えていたか…
　ここも、居心地が悪いと、来た道を少し戻り、門をくぐって方向を変えた。鳥が2匹飛んでいくのが見えた。
　
　どれくらいたったのか、それは空を見ればわかる。紅く染まり終え、これから夜が訪れる。
　来た道をもどれと言われても、もう覚えていない。初めてきたところであったし、戻る気などないから、仕方がないことなのだ。
　辺りに光が舞っている。夜に活発になる生き物の仕業。暗くないと目立たない、生き物の。光はとても数えていられないほど多かった。反射しているのだ。そこでようやく湖の存在に気づいた。草花の生える地にぽっかりと星々の空間が浮かぶ。このままひょいと乗ってしまえそうだった。そうして星の海を旅するのも、悪くない。
　もっと星の世界を視界いっぱいに広げようと、近づいて覗きこもうとすると、
　「なにやってんだ。」
　子供の声であった。どこかで聞いたことがある。それはとうとう思い出せなかったのだが。
　「湖を、星を見ていた。」
　「星はそとだよ。」
　水面を指差す。その子は隣まで駆けより、同じように覗き込んだ。
　「おおっ…」
　黒…というよりは、茶色だろうか、子供の髪が揺れ、その下からは、日に焼けた肌が見えた。髪が居場所を見つけとどまると、隙間から穏やかな黄緑が覗いた。しばらくそれに魅いっていると、少年は照れくさそうにした。
　「な、なんかついてるか。」
　「そういうわけではない。」
　今までの不快さが、彼を見とめたときから消えた。決して美しい姿ではないのだが。
　同時に、この少年はここに存在するべき者ではないのだと、何か、自分の中のなにかが告げていた。聞いてはいけない。君はどこから来たのかと。
　「もうすっかり暗いな。」
　それでも、話し相手くらいなら、許されるだろう。
　「今日は月が見えない日だ。」
　「ほんとだ。星ばっか。」
　腰にてをあて、天を仰いでいる。瞳は希望で輝いていた。きっと、今も。眩しすぎて、目をそらしてしまった。
　「人間は、星にまで名前をつけるんだよな。」
　「きいたことはある。ひとつも知らないけど。」
　「俺も。ぜってー覚えらんねぇ。」
　何も解決しない会話であった。この子でなければ、切り捨てていただろう、そんな、くだらない。
　「名前など、大した意味はないさ。」
　「ま、よーはわかればいいんだからな。」
　眺めていた位置より少し奥を中心に、水が波紋を作る。雨でも降るのかと、顔をあげた。空には星がいっぱいに広がるばかりで、他には何もない。
　…何もない。
　少年は、もういなかった。
　急に眠気が襲ってきて、横になれば、地面がひんやりと冷たい。湖がまたひとつ、波紋を作った。
　
　「遅いな…」
　もはや夕食は冷めきり、そろそろ眠りに落ちる時間にまでなっていた。最も、栄養を得ずとも死んでしまうような弱いものではないのだが、元気であることに、体力があるに越したことはない。
　「クレハ、何か聞いてはいないか。」
　「うーんと、景色のいいところへ行くって言ってた。…なんか怒ってるみたいだったけど…」
　眠い目を擦りながら、それでも兄が心配なのか、頑張って告げている。
　「景色のいいところへ…か」
　それは、どこか具体的に定まっているのか、それとも…どちらにせよ、私たちに居場所を伝えたくはなかったらしい。__怒っている。今朝の様子に、そのようなものは見受けられなかった。最も、彼は表情の変化が乏しい。今までにも、不満をためていたのかもしれない。全く…
　「お父さん…」
　「探しにいってくる。大丈夫だ、大体の検討ならつく。」
　「私も行く…」
　「もう遅い、ねなさい、クレハ。」
　クレハはしばらく渋っていたが、ベッドまで運ぶと、眠気には勝てなかったのかすぐに意識を手放した。
　さて、セラムの居場所は…
　　
　目が覚めたら、日が上っていた。それは至極当たり前のことなのに、ずっとあのまま夜なら、またあの子に会える気がして、少し気落ちした。
　立ち上がる気力はなかった。髪には土が絡まっているのだろう。昨日とは違った眩しさに目を細めつつ、流れ行く雲を眺めていた。
　ふと、ここはどこなのだろうとの疑問が起こる。だがそれも、それだけで終わっていった。
　もう、関係がない。どうせ、行くあても帰るところもありはしないのだから。
　湖が、何かを欲しがるように音をたてた。水鳥が魚をとらえに来たのか、数羽集まっていた。水は、翼が欲しいのか。その鳥たちを…食べてでも。
　手を伸ばし、水に宿る星の力をつかって、鳥を捕らえ…
　「何をしている。」
　その声に驚いて、手放すと水は勢いよく落下し、鳥は一匹残らず飛び立ってしまった。
　「星の力が異様に変化しているから来てみれば…セラム、やはりお前だったか。」
　怒られる…勝手に、自然に手を貸した。好奇心で命を奪おうとした…から。__よく、父にはその手のことで怒られている。力を簡単に使うなと。
　ゼクストは音をたてないように歩き、此方へ向かってきた。
　「心配したのだぞ。」
　…嘘だ。
　「さあ、帰ろう。」
　差し出された手には、見ることすら嫌気がさす。
　勝手に動かれると迷惑なんだ。調和が乱れるから…
　「…やはり、怒っているのか。何があった。」
　どうせゼクストは知っている。俺がクレハみたいに素直じゃないことも、笑えないことも。
　「教えてはくれないのか。」
　教えたくはないのに、答えない自分に、なんだか腹が立ってくる。恐る恐る顔をあげれば真剣な眼差しが見え、恐ろしくなってまた顔をそらす。ため息がひとつ聞こえた。
　と。
　急に体がふわりと浮く。驚きの声が漏れた。両脇の辺りに、なれない感触があり、やがて目の前に父が広がった。父の手の片方が足の方に回り、彼の腕に座る形になる。
　「拗ねているのか。すまないな、あまり構ってやれなくて。」
　「別に…父さんは、忙しいから、いい。」
　「…お前がそうやって強がるものだから、私は甘えていたのかもな。」
　花びらが待ったような気がして、それを目で追えば、優しい笑顔があった。自分も、自然と口許が緩む。
　「そうだな、今日はいっぱい遊ぼうか。星の欠片に残すのもいいかもな。」
　なんだ、こんなに簡単だったじゃないか。
　「クレハも…」
　「…そうだな、3人で。」
　不快な気持ちは、あの子が持っていってくれたのか…そのおかげなのか、今は心地よかった。体が、暖かさでいっぱいになった。冷たい地面も気持ちいいけど、こっちだって、悪くない。
　`
export const comment: string = `星の記憶や普段の言動見る限りセラム様って真面目というかシリアス(？)そうだけど、陽下だと楽しそうにしてるからさ･･･`;