export const novel: string = `「月明かり」という言葉のニュアンスを消し去るほど明確な光は、それなのにむしろ夜を実感させる。
　穏やかに吹く風が背に当たり、背中からじんわりと震えが伝わる季節。その気になれば暖かく変えることもできようが、今はこの寒気すら楽しみたいという空気がそこにはあった。
　「…入らないの。」
　と背後から優しい声がかかる。
　静かに首を振ると、隣に暖かさがやってくる。
　「ちょっとばかし…昔を思い出しててさ。」
　反射で光る糸をなびかせ、クレハがまた静かに問う。
　「…ナユタとシグナのこと?」
　「いいや、もっと昔だ。」
　そう、遥か昔。今の記録には残されていない、人と共存していた時。
　決して楽しいばかりではなかった、他のミトスの民や、人間の子どもらとの記憶…
　「そう。」
　クレハは呟いて、しばらく二人で海を見つめた。
　`
