export const novel: string = `いつものように外に出れば、いつものようにシグナが手紙を仕分けていた。それぞれの性格や、能力に応じて依頼を分担する。どちらが言ったかはもうわからないが、やっていけばいずれはそうなるだろう。
　シグナが最後の1つに手をかける。
　特殊な材質でできているのか、どこか瞬くように、儚げだった。シグナはそれを開けると、時が止まったように固まってしまう。何事かと裏から覗けば、そこには今までに見たことのないような文字が記されていた。
　「…何のはなしだ？」
　そう彼が呟いたのが、不思議でたまらない。なんだかわからないのはいいのだが。
　「え…シグナ、何て書いてあるかわかるの…？」
　「…いや、そうじゃなくて。」
　では、なぜ「何の話」と表したのか。そういった疑問が自分を占めて、
　「暗号かなんかじゃないかって。」
　その言葉でとれたような気がしたから、
　「あ、なるほど。」
　とそれまでにしてしまった。
　それは彼がついた小さな嘘の1つだ。見かけによらず、彼はよく嘘をつく。いや、ついていた、といった方が正しい。
　今ならわかる。あの文字は、レクセンドリアにあったそれと同じであった。だから、
　「あのときの手紙、本当はなんて書いてあったの？」
　「ん？何のはなしだ？」
　もう騙されない。その言葉が示すように、君は。
　「そう言うってことは、わかってるんでしょ。」
　まいったなと言わんばかりにこめかみのあたりに手のひらを当てると、ひとつため息をついて、白状した。
　「依頼じゃなくて、願いだよ。「戻ってこい」って。」
　それは、誰の。という言葉は、考え終わる前に塗りつぶされた。
　はずかしそうに目をそらす、「彼」に。
　`
