export const title: string = `セラアサへのお題は『知らないふりが上手くなった』です。`
export const novel: string = `　どこからか、花びらが舞う。私より一足先にたどり着いてしまうのだろうか。
　並ぶ店、家。まだだれもいないいつもの道。そのせいで、いつものという言い方が可笑しいような気さえしてくる。どうしてこんなに早く行こうと思ったのか…所謂神託なのかもしれない。よく言う夢で見た、というやつだ。
　想像通りに、彼はいる。
　あなたが呼んだの。
　振り向く瞳に優しい緑がないのに少し驚いて、はじめの言葉がでなかった。
　彼はなにも言わず、ほんの少し首を傾けた。
　あなたが呼んだんじゃない。
　「何がおかしい。」
　「何でもないわ。きっと私の思い違いだったのね。」
　それにはどこか納得したように、まだ暗い空を見つめる。
　太陽はでていないのに。
　「お出かけでもするの?」
　「…そのようなものだ。」
　「…ねぇ、「シグナ」君。」
　その続きは、反応がないことでまた音にはならない。
　「シグナ君?」
　「ん、ああ、なんだ。」
　優しい緑を閉じて、ばつが悪そうに微笑む。
　ふふ、良かった。
　「やっぱり、何でもないわ。」
　え…と分かりやすい表情に、また安堵する。
　「なんか、すっげえ気になるんだが…」
　それもそうかと何か別を考える。
　「そうね…今度また、お茶でもしましょうね。」
　また返事はない。だけど今度は、こちらをきょとんと見つめていた。
　`
export const comment: string = `タイトルからも分かる通り診断メーカーお題です。いや、どうしてセラアサ書こうと思ったんだ･･･`