


































import {Component, Vue} from 'vue-property-decorator';


// components
import TopMenuBar from '@/components/TopMenuBar.vue';
import TopPage from '@/components/TopPage.vue';
import ListPage from '@/components/ListPage.vue';
import ContentPage from '@/components/ContentPage.vue';
import About from '@/components/About.vue';

// model
import {GroupItem, PageItem} from '@/model/objects.ts';
import {nayutaGroups} from '@/model/titles/nayutaGroups.ts'
import {ffGroups, rfGroups, xlGroups, otherGroups} from '@/model/groups.ts';

const Title = {
  Nayuta: "那由多の軌跡",
  FF: "Final Fantasy",
  RF: "Rune Factory",
  XL: "xblaze(lost:memories)",
  Other: "その他"
} as const
type TitleType = typeof Title[keyof typeof Title];
type PageType = "top" | "list" | "page" | "about";

@Component({
  components:{
    TopMenuBar,
    TopPage,
    ListPage,
    ContentPage,
    About
  }
})
export default class Main extends Vue{
  titles: TitleType[]= [Title.Nayuta, Title.FF, Title.RF, Title.XL, Title.Other]
  currentPage: PageType = "top";
  currentTitle: TitleType = Title.Other;
  currentIndex = 0;
  currentGroupIndex = 0;
  currentGroups:GroupItem[] = []
  currentItem: PageItem = nayutaGroups[0].items[0];
  
  selectTitle(select:string){
    switch(select){
      case Title.Nayuta:
        this.currentTitle = Title.Nayuta;
        this.currentGroups = nayutaGroups;
        break;
      case Title.FF:
        this.currentTitle = Title.FF;
        this.currentGroups = ffGroups;
        break;case Title.RF:
        this.currentTitle = Title.RF;
        this.currentGroups = rfGroups;
        break;case Title.XL:
        this.currentTitle = Title.XL;
        this.currentGroups = xlGroups;
        break;case Title.Other:
        this.currentTitle = Title.Other;
        this.currentGroups = otherGroups;
        break;
      default:
    }
    this.movePage('list');
  }

  selectContent(select:{index: number, item: PageItem, groupIndex:number}){
    this.currentItem = select.item;
    this.currentIndex = select.index;
    this.currentGroupIndex = select.groupIndex;
    this.movePage('page')
  }

  movePage(target: PageType){
    this.currentPage = target;
    window.scrollTo(0,0);
  }

  moveContent(next: boolean){
    this.currentItem =  next ? this.currentGroups[this.currentGroupIndex].items[--this.currentIndex]
      : this.currentGroups[this.currentGroupIndex].items[++this.currentIndex];
    window.scrollTo(0,0);
  }
}
