export const novel: string = `　穏やかに吹く風に揺れる花の色。
　冷たくも清らかな白い石。
　優しさに溢れた、本来の魔獣の姿。
　夜のガラスのように映る水面。
　4つの季節をほどよく混ぜこんだ木々。
　何処までも届きそうな建物。
　そして、辺りに舞う、命の光。
　それはかつて神が眠っていた、|失われた楽園&lt;&lt;ロストヘブン&gt;&gt;ことテラの庭園を思わせる風景だった。
　「どうだ。」
　そう得意気に微笑む友に、感嘆の言葉しかでてこない。
　初めてココへ訪れたときのような高ぶりがよみがえる。しかし今度は、ようやくたどり着いたという理由ではない。
　ここで、今が始まった。そんな懐かしさからだった。
　「まだ詰め込んだ感じが強いけどな。」
　「それでもすごいよ!こんな世界を作れるなんて…」
　近頃の素っ気ない態度を思い出す。受け答えの口調こそ彼のそれではあったのだが、依頼の打ち合わせ以外付き合いが悪く、一時はどうなることかと思っていた。
　しかし今日、ふいにシグナがあのときのように声をかけてくれる。まるで、それまでの冷たさなどなかったかのように。
　そうして連れてこられた亜空間は、以前のそれとは見違える美しさであった。
　「こっちに来てみろよ。」
　辺りを遠く眺めていると、そう言って手を引っ張られる。すぐに体が反応せず、つまずきそうになるが、なんとか体制をたて直し、後に続く。彼の握る手が、いつものように冷たいことだけに不安が残った。
　「これは…」
　宝石をちりばめたような、どこか小さい、宇宙。それは残され島からの星空と同じものであった。
　「今の時期の残され島からの星だね。」
　「げ、わかっちまうのかよ…なんつーか、流石だな…」
　呆れながらも褒められ、今度は僕が得意気に微笑む。
　「でも、どうしてまた?もしかして、星に興味持った?」
　「クレハがさ…昔アストロラーベの空が好きだって言ってたの思い出して。どうせならって再現してみたんだ。」
　シグナがいつもより丁寧に微笑んだのを見て、暖かな気持ちになる。たしかに彼女は綺麗だと見つめていたなと思い出す。
　「つっても、俺は詳しくねーし、残され島から星見て位置とか輝きとか決めてみたら、こうなったわけだ。」
　そういえば以前からアストロラーベでの星々が一体どういう原理なのか気になっていた。
　…シグナに教えてもらうのは少しシャクだが。
　「これって、どうなってるの?」
　「おーやっぱそこくるか…何て説明していいのやら…」
　うんうんとしばらく唸ってから、少しずつ説明してくれたのだが、彼の説明からすべてを理解するのは難しそうだった。
　その話はまた少しずつするとして、次へ向かう。そこは、何かの制御室のようだった。操作端末のような複雑な機械が置かれ、中央には青白く瞬く結晶体が安置されている。
　尋ねても、今度は何も答えなかった。一体どうしたのだとの言葉がでかけて、急いで隠れていった。シグナが片腕を伸ばしたのに驚いたからだろうか。
　彼が翼のある方の手を伸ばし握ると、結晶体は派手な音をたて、粉々に砕け散り、その欠片もやがて辺りに消えていった。目玉が飛び出しそうなほどまぶたを開けているのが自分でもわかる。やはり声はでなかった。
　と。
　「あ～やっぱ最後に確認しとくんだったな。」
　その声音はシグナのもので、先程までの表情からは裏返したようなものだった。
　「ちょっと待っててくれ。」
　「あ…うん。」
　あまりに拍子抜けしたためか、そんな返事しかできなかった。
　シグナは再び瞳を黄金に染めると、創造を始めた。見る限りでは、先程となにも変わらない、蒼く瞬く結晶であった。彼は一息つく。
　「これは星の力の結晶…みたいなもんだ。」
　「結晶…？けど、どうして一度壊したりなんか」
　「いやな、少しばかり力が辺りに流れ出ていたからさ。」
　なんだかシグナらしくない。
　どこか弱気で繊細な、遠くを見透かしたような答えだった。
　「それで?まさか見せびらかすためじゃないでしょ。」
　再び外に出て、尋ねる。
　彼の中で僕の存在がどんなものかはわからない。ただ、セラムが、クレハ…というよりミトスの民以外のものを快く思っていないだろうことは感じている。
　「はは、さすが。…実は、」
　続きを待っていると、突然肩を押される。バランスを崩し倒れると、彼の後ろには不気味な渦が現れる。
　あのとき、星座球やセラムが使っていた、転移魔法。
　「待って…!」
　腕を伸ばして立ち上がるも、もう遅い。シグナは闇に飲まれていった。
　自分の叫びが空しく響く。
　また、穏やかに風が吹いた。
　`

