export const title: string = `座した星の焦燥`
export const novel:string = `　それはまるで、本当の子のように愛しかった。抱きつく腕からは、実の父でもないのに、喜びを感じる。
　星の姫はすぐに父と呼んでくれた。
　星の騎士も、他には見せない柔らかな笑みを向けてくれた。
　やはり、正しかった。
　この姿で、彼らをこれからも、見守ろう___
　
　やがて星の子らは永き眠りにつく。
　きたるべき時のため、来ない日を祈りながら__
　安らかな寝顔を眺めているだけで、十分であった。
　これが、親の心というものなのか、ただの主に対する忠誠なのか。
　どちらでも構わない。ここに、居られるのならば__
　
　永い永い、時が過ぎた。
　自分が何者であるのか、忘れてしまうほどに__
　もう、彼らは目覚めないのだろうか。
　もう、星への干渉は、生き物の導きは__我らの使命は。
　使命。その言葉が、ひどく引き付ける。
　私がここにいる意味。
　彼らが眠る意味。
　星の悲鳴。
　何も終わっては、いない。
　それでは私は無意味になる。
　私は、私の果たすべきコト…
　
　今、立ち上がる。
　`;
