export const title:string = '師弟'
export const novel:string =`　風が変わった。
　男はなにも言わぬまま、また、身動きひとつしなかったが、気配は感じ取っていた。
　どこか懐かしくも、冷たい。対峙可能な者にこれ程冷たいものがあったものかと。
　「オルバス・アルハゼンと言ったか…」
　青年は、振り向きもしない剣豪に確認するだけのように口をきいた。
　「何者かは知らぬが…」
　名を呼ばれてようやく視線を向け、続けようとしたが、
　目の前の青年と弟子でもある息子が重なり、思わず口をつぐむ。
　仮面で顔が見えぬとはいえ、年頃以外その2つは似ても似つかないのだが。
　「…」
　青年もまた、それ以上は口にしない。
　風の音だけが、辺りを包み込む。
　荒々しくも冷たく、青年をよりいっそう離れさせるように。
　「何用だ。」
　剣豪はそれだけ問うた。
　「…1度は、勝っておきたいと思ってな。」
　今初めて会ったばかりのオルバスに、無論心当たりはない。
　あるいは、シグナならその言葉もわかるのだが。
　「いや、あいつを本当に消し去るため、か。」
　それもまた、解らなかった。
　言葉のわりに、青年は薄く笑う。最も、その笑みすら凍りついていたのだが。
　「その言葉の真意はわからぬが、私を相手にするのなら手加減はできぬぞ。」
　常人なら血相をかえて逃げ出すであろう凄みにもまた、冷えきった表情でかえす。
　
　金属同士が触れあう、鈍い音。
　なるほど師弟とはよくいったもので、青年は神殿での殺し合いを思い出していた。
　だが、あのときほど心には響かない。結局、"俺"にはどうでもいいことだったのだろうか。
　
　そのときの、諦めたような表情が、また、思い出の中の息子と重なる。
　普段は明るい彼が時折見せる、何か吹っ切れたような影の顔。
　いつもは全く興味のない空を１人見上げている、あの__
　一瞬ともいえぬ時、隙をつくる。
　他のものならいざ知らず、それは青年には十分すぎた。
　より鈍い音がした。
　ついで、生暖かい感触と、鋭い傷み。即死こそ免れたものの、もはや勝敗は明白だった。
　目の前に差し出された大剣を、男が見つめる形で対峙する。
　負けたものなら、殺されても仕方がないとは思ったのだが…
　「一瞬の隙も見逃さぬとは、見事。私も老いたかな。…青年よ、名を」
　「お前などが知ることはない。」
　しかし相変わらずの態度で払われる。
　その言葉は、果たしてどちらを下と見て言ったのか。
　オルバスはひとつ息をはいた。
　そして__
　
　暗い、暗い。
　俺が初めて俺となり、やがて俺は完全な俺となるため消されるのだろう。
　そう、予想はしていたが、
　まさかそのような形を取るとは思わず、やめろとつい声がもれるも、白い笑みに押し潰される。
　瞳が変わるかと思うほど、支配された空間。
　鈍い音が心をかきむしる。
　あるはずのない早鐘が、音のすべてに感じる。
　俺が果たしたかった目標_
　俺であって俺でないものが、俺であることを使って、父を殺す。
　かつて俺がされたように__
　そうしてまた笑う。
　笑っている心地がしない、冷えた笑み。伸ばしたい手ももはや存在せず、全てが白に飲まれていく。　
　「さらばだ。…そして、」
　目を開けば、蝶よりも優雅に舞う光が目にはいる。
　結局人間は理解できない世界の源。
　万物を司る光なら、きっと覚えているだろう。
　"シグナ"が、一体何を意味するのかを___
　
　
　
　
　__そんな、夢を見た。
　それはないと、あいつはまた笑った。
　`

export const comment:string = `　那由多の軌跡の前にプレイしていたゲームの影響で、
　おかしくなったシグナが師匠を殺しに来るんじゃないかとひやひやしていました。
　けどよく考えたら鍛錬システム的にそれはないか←`
