export const title: string = `虹に輝く葉`;
export const novel: string = `※捏造注意
　「クーレーハちゃん。」
　誰かの声が妹を呼ぶ。
　そういえば昨日も、その前も、彼らは同じ時刻にやって来た記憶がある。
　クレハは朝食もそのままに飛び出そうとして、いつか父に静かに指摘されたを思い出してか、急いで片付け始める。
　「こらこら、喉につまってしまうぞ。」
　そんな姿は見たくなくて、
　「セラム、まだ終わっていないだろう。どこへいく。」
　「すぐ戻る。」
　そうして向かった出入り口には、声の主の他2人ほどが待ち構えていた。
　「いま朝食をとってるから。先に遊んでればいい。」
　ミトスの民と人間の子供らは顔を見合わせ、それじゃあと、時々こちらを覗きながら去っていく。
　部屋に戻り、先ほどのことを話せば、クレハは落ち着いて食べ始めた。
　「兄さん、どこに行ったって。」
　丁寧に皿を運びながらクレハは尋ねた。そういえば、聞いてない。
　「もう…そういうとこあるんだから。」
　怒っていると思えば、クスクスといった感じに笑っていた。
　…何が面白いのだろう。
　「心当たりはあるから」と、今度は本当に飛び出していった。
　その後父も何やら話し合いがあるとかで出ていった。
　
　
　「あークレハちゃん。」
　同じミトスの民の子が一早くこちらに気づき手を振った。
　花畑。たくさんの色の、種類の、覚えきれない花たち。
　何してるのと尋ねれば、花で作られたわっかを目の前につき出される。
　「花かんむり。教えてもらってるの。」
　側で人間の子が照れたように笑っている。
　クレハちゃんもつくる、ともう一人から尋ねられ、頷く。
　完成したら、どうしようかは考えるまでもなかった。
　ここをこう、これを回して、と教え方が上手で、順調に出来上がっていく。決して乱暴でない扱いに花も満足なのだろうか。
　「そういえばさあ、クレハちゃんのおにいちゃん、こわくない。」
　教えてくれている子が、皆何となくわかってきて持て余したのか、そう尋ね、疑問の声が短く漏れた。
　「そうよね、さっきも、やっぱり…」
　一体兄はどんな風に説明したのかと苦笑いが漏れる。
　そんなことないと、確かに静かだが、優しいのだと言おうとして、
　「ねえクレハちゃん、どうしてセラムさんはクレハちゃんやゼクストさまみたいに、瞳の色が暖かくないの。」
　え…
　考えたこともなくて、また同じ意味の声が出てしまった。
　「あの目、やっぱりこわいよね。」
　そうなのだろうか。
　たったそれだけで、この子たちは。
　「そ、んなことない。兄さんは優しいもん。」
　少し苛立ち、自然と声を張る。
　すると、ごめんねと、そうだよね、見た目だけで言うのはよくないって言われてたと、謝られて、私も謝った。
　「でーきた。」
　沈黙の後、誰かが声をあげ、それに続いた。
　
　
　出入り口から見れば、ちょうど裏に当たるだろうか―そこにその木はあった。自分の背丈の何十倍もある。父さんだって 、他の大人だって、一番上は普通には見えない。太さも、中に空洞があったら、数人入れるのではと思う。
　そっと幹に触れる。
　ごつごつしているのに、肌触りが悪いわけではなく、どこかあたたかい。
　ほんの少し心のなかで思って、気に入りの幹に移動する。
　葉と葉の間から下が見える。森の開けたところで、複数の影が見える。
　大人たちの話し合いだろうか。
　東に見える家から１つ影が飛び出した。大きな鳥は小鳥のように、地を歩き、池に水しぶきが上がる。
　はらり、と1枚葉が頭へ落ちてきた。眺めれば、どうやら朽ちかけているようで、軽く、落ちないように握るだけで、枯れ葉特有の乾いた音がした。
　星の力の、色が、流れが、変わりかけている。このまま落としてしまえば、土にとけてしまうだろう。
　試すような気持ちで、流れを少しいじってみる。葉はみるみる色を取り戻す。緑に戻り、黄を経て紅へと。
　だけどそれだけじゃあと、さらに続ける。そうして磨きあげたガラスのように、清らかな水のように透き通った。
　下で微かに声が聞こえたような気がして、ほぼ真下を覗くと、数人の男児がいた。
　「なにやってんだ。」
　答えようとして、下へひょいと飛び降りる。最も、着地する手前で、少し体を浮かせたので、この高さでも怪我はしなかった。
　「あービックリした。あぶねーだろ。」
　と人の子が口を腕で隠すようにした。
　幹についていたところを持ち、先ほどの葉をかざすと、おおと感嘆の声が上がる。
　「すっげーなにそれ。」
　「どこで見つけたんだ。てっぺん。」
　問われて首を振る。
　「創った。」
　「は…」
　同じミトスの民は頷いていたが、他の子は困惑が隠せないようであった。
　「まあいいや、なあセラム。一緒に遊ぼうぜ。」
　特にやることもないので、首を縦にふり、了承すると子供らは駆け出した。そっと、先程頷いた子が、あんましやらない方がいいぜ。と耳打ちした。
　
　「ここだよ、ほら。」
　そう案内されたのは、一番年上の子がやっと入れるほどの小さな洞穴であった。
　「何があるんだ。」
　「ばっかだなぁ、それがわかんねーから行くんだぞ。」
　ほら行った行ったと急かす少年は、自ら入ろうとしない。他の子も譲り合っている。それならばと名乗りを上げようとすると、最年長の子がオレについてこいと胸を張った。
　…恐いならやめればいいものを。
　だがその探検は一瞬にして終わる。
　遠くで高い声が聞こえた。
　その声の主に気づいて、少年たちは顔を見合わせ、我先にとそちらへ向かった。 
　
　くるくると踊ったり、手を胸に当てて歌ったりと、できたばかりの冠で遊んでいた。
　かさと聞こえた草の音をふとみると、見慣れない生き物がいた。
　点が集まって、大きな虫のような形をしている。
　驚いて、みんな口々に叫んだ。
　逃げなきゃ、こわい。
　それがよくわからなくて、立ち止まっていたら、腕を引っ張られた。その力があまりにも強くて、転けてしまいそうになる。
　「クレハちゃん！」
　訳のわからぬまま走っていると、やがて行き止まり。左右は開けているが、目の前の大きな岩に圧倒されて、そう思った。
　大きな虫はこちらへ向かっている。
　みんなは目をつぶってもうだめだと口々に呟いている。気のせいだろうか、虫は怒っていなさそうなのだが。
　点が散ろうとすると、何処からともなく声がかかり、数人の子供らが手に木なりを持ち、虫と私たちの間にはいる。少し離れて兄もいることに気づく。兄もまた、納得がいかないような顔をしていた。
　
　1人が勢いよく得物を振り回す。
　しかし案の定と言うべきか、虫にダメージはない。
　なぜ彼らはそんなものでどうにかなると思っているのだろうか。答えは簡単だ。彼らが、人間だから。
　年長の、ミトスの子が手を掲げる。少し間を開け、勢いよく火の手があがる。
　虫たちは驚き、逃げていった。すぐに炎は消えた。子供たちも次々と座り込んだ。誰かはさすがと叫び、誰かは大丈夫かと声をかけている。
　「にいさん。」
　クレハが側に来ていた。他の子のような怯えはみえない。どちらかというと、合点がいかないようである。
　「怖くなかったか。」
　先程火を創った少年がクレハを気づかった。クレハは首を横に振り、
　「怒っているようにはみえなかったもの…」
　場の空気が変わるのがわかる。
　視線は一点に集中し、そのどれもが心地よいものではない。それに怯えたクレハが、後ろに隠れるように顔を伏せると、強がっていたと誰かが呟く。
　そうして興味を失って、やがてそれぞれ散っていった。
　2人だけ取り残されると、ようやくクレハは顔をあげた。
　「私、変なこと言ってない…」
　静かにうなずくと幾らかは安心したようだが、震えはまだ伝わっている。
　そういえばと先程の葉をとりだす。
　クレハは目を大きくして何かと尋ねる。
　「葉だ。少しいじったけど。」
　今度は目を細め、素敵だと、綺麗だと喜んだ。クレハは透き通る葉を受け取り、日の光に掲げるように見て、いっそう目を細めた。それがどうにも愛しかったせいか、もっと驚くことを、喜ぶようにと今度は葉の色を様々に変えてやる。はじめこそ目を見開いていたが、すぐに笑顔に変わる。すごいすごいと、くるくると回った。それに合わせて周りの草花の色も変える。まるで花の精のように漂い、踊った。平和なときであった。花びらは日の色に、夜の色に…葉は水の色に、月の色に、星の色に___バラバラと変わるのに、不思議と調和がとれていた。まるで1つの曲を奏でるように。――それは俺自身の心が澄んでいたせいなのだろうか――
　やがて疲れたのか、クレハは踊るのをやめ、側に座り込んだ。
　「あーたのしかった。」
　少しばかり息は荒かったがその顔から笑顔は消えていなかった。
　「やっぱり、にいさんはすごいね。」
　「クレハにだってできるさ。」
　ほんと、といっそう瞳の輝きが増す。
　「ほら、ここをこういうふうに…」
　少し教えてやれば、すぐに葉や花の色を変える術を覚える。
　「魔法にもならない簡単なものだ。」
　「ふふ、ありがとう。」
　その笑顔は……俺には眩しすぎた。
　
　それから数日たった頃だった。
　食事の片付けをする人をボーッと見て、さて今日は何をしようかと考えているところに、勢いよく扉が開かれ、今にも泣きそうな…いや、すでに目は赤く、水分を多く含んでいた。
　「にいさん……」
　とてとてとこちらに近づくのはクレハだ。
　椅子からぴょんと降りて、先程また遊びにいくと飛び出していったはずのクレハと視線を合わせる。
　「ううっ…ひっく……」
　クレハは静かに―苦しそうに泣き始めた。どうしたらいいかなどわからなくて、とにかく自分はここにいることを、側にいることを示そうと小さな肩をつかむ。
　「にいさん……」
　「…………」
　なんと声をかければよいのだろう。きっと父さんなら、知っている。クレハが一瞬で笑顔になる言葉を。嫌なことも忘れてしまう物語を。でもいま彼は忙しいのだ。
　「……また、はなかんむり、つくろうって…」
　クレハは涙をためながらゆっくりと話し出す。
　「だから、私…色を変えたらもっと…いいかなって。そしたら…」
　気味が悪い。
　つまりはそのようなことを、適した表情と共に呟かれたそうだ。心の中から何かが這い上がってくる。
　「そんなこと、ないよね。きれいだよね……」
　クレハはどこからか、くしゃりとつぶれた虹の花を取り出した。―それはおそらく、彼女が握りつぶしたものではない。
　ぎゅっと手のひらに力がこもる。
　自分では押さえられない何かが、俺を包んで……
　「だって、にいさんのおしえてくれたことだもの……」
　そのまま、「俺」になる前に。
　――え？
　はっと視界が甦って、クレハが俺にしがみつきながらも首を傾けている。何かは再び俺の奥へと降りていく。
　「にいさんが、せっかくわたしにおしえてくれた花だもん。きれいなの、絶対。」
　「クレハ……」
　この子は…妹は、自分の創ったそれにけちをつけられて、怒っていたのではない。何も知らない人間の、心ない言葉を、そのうちに含まれるものを必死で否定しようとしていた。
　「……ありがとう。」
　「……？」
　ああ、そういえば。
　ぎゅっと、今度は優しい力を腕に込め、彼女を抱き締めた。
　「…ふふ、あったかいね。」
　昔父さんがやってくれた 。こうすると、とても暖かいのだ。
　そう伝えると、
　「…やっぱり、にいさんはなんでも知ってるのね。」
　片耳で俺を聞くように、静かに目を閉じたクレハは、そのまま泣きつかれてどっと体重を預けた。小さな身体を腕よりも先に思考が支えた。ふわりとクレハの身体が浮く。ぽとりと手から落ちて音をたてた。
　それに気づいたのは、クレハを彼女の部屋へ転移させたあと。くしゃくしゃの花を拾い上げて、この暖かさを、綺麗なものを、忘れないように。
　花をそっと、星の記憶に変えた。
　`
export const comment: string = `兄妹仲良いよねかわいい。
実際ミトスの民って他にどれくらいいたんだろう…？`;