export class HistoryItem{
  private title: string;
  private date: string;

  constructor(title: string, date: string){
    this.title = title;
    this.date = date;
  }
}

export class ContentItem{
  private src: any;
  private comment: string;
  constructor(src: any, comment: string){
    this.src = src;
    this.comment = comment;
  }
}
export class PageItem{
  private title: string;
  private contents: ContentItem[];
  private _isNobvel: boolean; 
  constructor(title:string, contents:ContentItem[], novel=false){
    this.title = title;
    this.contents = contents;
    this._isNobvel = novel;
  }

  get isNovel(){
    return this._isNobvel;
  }
}

export class GroupItem{
 private groupName: string;
 private _items: PageItem[]
 constructor(groupName: string, items: PageItem[]){
   this.groupName = groupName;
   this._items = items;
 }
 get items(){
   return this._items;
 }
}