export const novel: string = `　「ごめんね、わざわざ。」
　ベッドの上に横たわり、顔だけこちらを向けてアーサは言う。
　シグナは首を振って大事でなかったことに安堵すると、そばの椅子に腰かけた。
　「ったく、ナユタのやつ。」
　先程家に疾風のごとくナユタが駆け込んできた。アーサが倒れたと報告する顔は真っ青で、それこそ具合が悪いようであった。数年前、アーサは奈落病に蝕まれていたため、それがぶり返したのではと、その時の彼を思い出させる口調でいるのをクレハに任せ、飛び出せばただの風邪だとアーサは苦笑いする。
　「それにしても、無茶するなっての。」
　「ふふ、私もダメね。昨日は冷えたから。」
　季節の変わり目であるため冷えは急にやって来た。動いていなければ、薄着など到底我慢できないであろう。特に日の光が隠れる夜などは。
　「ちょいと失礼。」
　そう言い、シグナはアーサの手に触れる。少し火照った顔は不思議そうに彼を眺めていた。
　やがてアーサは体をめぐる空気が清んでいくような感覚に襲われ、自然と口許が緩んだ。
　「すぐに回復すると思うが…今日は寝てな。」
　すっと手の甲の暖かみが消えていく。しかしそれ以外は気が軽かった。
　「じゃあ。」と去ろうとすれば、「もう少し、」と微笑む。
　特に用事があるわけでもなく、ここで彼女を無視していく意味も見いだせなくて、シグナは再び椅子に体を預けた。
　引き留めたとはいえ、別に話したいことがあるわけでもなく、もて余していると、アーサの頭にひとつ浮かんだ。
　「シグナくん…好きな食べ物、何かしら。」
　何を今更と目を丸くしたシグナは、それでもすぐには出てこなくて、唸り、傾き、アーサの飯は何でもうまいと思うと濁した。最も、実際そうは思っているのだが。
　「そういう優しいのはいいの。」
　そうは言われても、と頭に手をやりながら思考を巡らすシグナを、穏やかな時をすごしアーサは見つめる。
　「そーだなぁ。じゃ、ステーキとか？」
　好きな食べ物というよりは、食べたいもの、であったが、アーサは納得したように微笑んだ。
　「明日、また、いらっしゃいね。」
　
　「姉さん、行ってくるね。」
　「行ってらっしゃい。楽しんできてね。」
　ナユタは駆け出していく、賑わい始める前の店へ。そこにはライラが相変わらずの赤い顔で待っていた。
　「あ、おはよ。…そういえば、アーサさん大丈夫。昨日、熱出したって。」
　「うん、体冷やしちゃったみたいでさ、でも、もう大丈夫だよ。」
　ならよかったと、ライラは微笑み、二人は準備にとりかかった。
　`
